import { BlogConfig, BlogContent, TargetDrive } from '@youfoundation/transit-lib';
import { FC, useEffect, useRef, useState } from 'react';
import { Link } from 'react-router-dom';
import useBlogPosts from '../../../../../hooks/blog/useBlogPosts';
import { ChannelDefinitionVm } from '../../../../../hooks/blog/useChannels';
import Arrow from '../../../../Icons/Arrow/Arrow';
import Image from '../../../../Image/Image';

require('./BlogChannelTeaser.css');

interface BlogChannelTeaserProps {
  className?: string;
  title: string;
  channel: ChannelDefinitionVm;
}

export const BlogChannelTeaser: FC<BlogChannelTeaserProps> = ({ className, title, channel }) => {
  const [index, setIndex] = useState(0);
  const [disableAutomaticIndexCalculcation, setDisableAutomaticIndexCalculation] = useState(false);
  const [maxIndex, setMaxIndex] = useState(1);
  const scrollContainer = useRef<HTMLDivElement>(null);

  const { blogPosts } = useBlogPosts({ channelId: channel?.channelId }).data ?? {};

  const doScroll = (direction: number) => {
    if (scrollContainer?.current) {
      const newIndex = index + direction;
      setIndex(newIndex);
      setDisableAutomaticIndexCalculation(true);

      scrollContainer.current.scrollTo({
        left: newIndex * scrollContainer.current.clientWidth,
        behavior: 'smooth',
      });
      setMaxIndex(scrollContainer.current.scrollWidth / scrollContainer.current.clientWidth - 1);
    }
  };

  const calculateIndex = () => {
    if (scrollContainer?.current && !disableAutomaticIndexCalculcation) {
      const calculcatedIndex =
        scrollContainer.current.scrollLeft / scrollContainer.current.clientWidth;
      setIndex(calculcatedIndex);
    }
  };

  useEffect(() => {
    if (disableAutomaticIndexCalculcation) {
      setTimeout(() => {
        setDisableAutomaticIndexCalculation(false);
      }, 1000);
    }
  }, [disableAutomaticIndexCalculcation]);

  useEffect(() => {
    // calculate index on load to ensure that there was no scroll state enforced by the browser
    calculateIndex();
  }, []);

  return (
    <section className={`body-font overflow-hidden ${className ?? ''}`}>
      <div className="container mx-auto px-5 py-6">
        <h2 className="mb-5 text-2xl text-gray-600 dark:text-gray-300">{title}</h2>
        <div className="relative">
          <div
            className="overflow-vertical-hidden overflow-horizontal-scroll -m-1 flex flex-nowrap"
            ref={scrollContainer}
            onScroll={calculateIndex}
          >
            {blogPosts?.map((blog) => {
              return (
                <BlogTeaser
                  className="w-1/2 sm:w-1/3 md:w-1/4 xl:w-1/6"
                  blog={blog}
                  linkRoot={`/home/blog/${channel.slug ? channel.slug + '/' : ''}`}
                  key={`${blog.channelId}_${blog.id}`}
                />
              );
            })}
          </div>
          {index !== 0 ? (
            <div className="absolute top-0 bottom-20 left-[-1.6rem] flex w-14 flex-col justify-center bg-opacity-25 px-2">
              <button
                className=" rounded-full bg-gray-100 p-2 dark:bg-gray-900"
                onClick={() => doScroll(-1)}
              >
                <Arrow className="rotate-180" />
              </button>
            </div>
          ) : null}
          {index < maxIndex ? (
            <div className="absolute bottom-20 top-0 right-[-1.6rem] flex w-14 flex-col justify-center bg-opacity-25 px-2">
              <button
                className="rounded-full bg-gray-100 p-2 dark:bg-gray-900"
                onClick={() => doScroll(+1)}
              >
                <Arrow />
              </button>
            </div>
          ) : null}
        </div>
      </div>
    </section>
  );
};

interface BlogTeaserProps {
  className: string;
  blog: BlogContent;
  linkRoot: string;
}

const BlogTeaser: FC<BlogTeaserProps> = ({ className, blog, linkRoot }) => {
  const imageTargetDrive: TargetDrive | undefined = blog.channelId
    ? {
        alias: blog.channelId,
        type: BlogConfig.ChannelDriveType.toString(),
      }
    : undefined;

  return (
    <div className={`mb-0 flex-shrink-0 p-1  ${className}`}>
      <Link to={linkRoot + (blog.slug ?? '#')} className="contents">
        <div className="aspect-video overflow-hidden transition-transform duration-300 hover:scale-110">
          {imageTargetDrive && blog.primaryImageFileId ? (
            <Image
              className="h-full w-full object-cover object-center"
              fileId={blog.primaryImageFileId}
              targetDrive={imageTargetDrive}
              alt="blog"
              cover={true}
            />
          ) : (
            <div className="flex h-full items-center bg-slate-200 p-2  text-center dark:bg-slate-700">
              {blog.caption}
            </div>
          )}
        </div>
        <div>
          <h2 className="title-font text-md fade-overflow max-h-14 pt-2 font-medium text-gray-900 dark:text-gray-200">
            {blog.caption}
          </h2>
          <span className="text-gray-500 md:pt-1 md:text-sm">
            {new Date(blog.dateUnixTime).toLocaleDateString()}
          </span>
        </div>
      </Link>
    </div>
  );
};
