import { useQuery } from '@tanstack/react-query';
import {
  ApiType,
  ChannelDefinition,
  ChannelTemplate,
  PublicClient,
} from '@youfoundation/transit-lib';
import { convertTextToSlug } from '../../helpers/common';
import useAuth from '../auth/useAuth';

type useChannelsProps = {
  channelSlug: string;
};

export interface ChannelDefinitionVm extends ChannelDefinition {
  slug: string;
  template: ChannelTemplate;
}

const useChannels = ({ channelSlug }: useChannelsProps = { channelSlug: '' }) => {
  const { getSharedSecret } = useAuth();

  const parseTemplate = (templateId: number | undefined) => {
    return parseInt(templateId + '') === ChannelTemplate.LargeCards
      ? ChannelTemplate.LargeCards
      : parseInt(templateId + '') === ChannelTemplate.ClassicBlog
      ? ChannelTemplate.ClassicBlog
      : ChannelTemplate.MasonryLayout;
  };

  const fetchChannelData = async (channelSlug: string | undefined) => {
    const client = new PublicClient({ api: ApiType.YouAuth, sharedSecret: getSharedSecret() });
    let channels: ChannelDefinitionVm[];

    const fileData = await client.fileReadOnlyProvider.GetFile('channels.json');

    if (fileData.has('channels')) {
      channels =
        fileData.get('channels')?.map((entry: any) => {
          const item = entry.payload;
          return {
            ...item,
            slug: convertTextToSlug(item?.name),
            template: parseTemplate(item?.templateId),
          } as ChannelDefinitionVm;
        }) ?? [];
    } else {
      channels = (await client.blogPostReadonlyProvider.getChannels()).map((item) => {
        return {
          ...item,
          slug: convertTextToSlug(item?.name),
          template: parseTemplate(item?.templateId),
        } as ChannelDefinitionVm;
      });
    }

    // Active Channel
    const activeChannel = channelSlug
      ? channels?.find((channel) => convertTextToSlug(channel.name) === channelSlug) ?? undefined
      : undefined;

    return { channels, activeChannel };
  };

  return useQuery(['channels', channelSlug], () => fetchChannelData(channelSlug), {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
    staleTime: Infinity,
  });
};

export default useChannels;
