import { useQuery } from '@tanstack/react-query';
import {
  ApiType,
  AttributeDefinitions,
  BuiltInProfiles,
  PublicClient,
  MinimalProfileFields,
  Attribute,
} from '@youfoundation/transit-lib';
import useAuth from '../auth/useAuth';

const useBiography = () => {
  const { getSharedSecret } = useAuth();

  const fetchData: () => Promise<
    { title: string; body: string; id: string }[] | undefined
  > = async () => {
    const client = new PublicClient({ api: ApiType.YouAuth, sharedSecret: getSharedSecret() });

    const fileData = await client.fileReadOnlyProvider.GetFile('profile.json');
    if (fileData.has('bio')) {
      const bioAttributes = fileData.get('bio')?.map((entry) => {
        const attribute = entry.payload as Attribute;

        return {
          title: attribute.data[MinimalProfileFields.ShortBioId] as string,
          body: attribute.data[MinimalProfileFields.FullBioId] as string,
          id: attribute.id,
        };
      });
      if (bioAttributes?.length) {
        return bioAttributes;
      }
    }

    const biographyAttributes = await client.profileDataProvider.getAttributeVersions(
      BuiltInProfiles.StandardProfileId.toString(),
      BuiltInProfiles.PersonalInfoSectionId.toString(),
      AttributeDefinitions.FullBio.type.toString()
    );

    return biographyAttributes?.versions.map((attribute) => {
      return {
        title: attribute.data[MinimalProfileFields.ShortBioId] as string,
        body: attribute.data[MinimalProfileFields.FullBioId] as string,
        id: attribute.id,
      };
    });
  };

  return useQuery(['biography'], fetchData, {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
    staleTime: Infinity,
  });
};

export default useBiography;
