import { ReactNode } from 'react';
import useBiography from '../../../../../hooks/biography/useBiography';

const Biography = ({ className }: { className: string }) => {
  const { data } = useBiography();

  if (!data) {
    return <></>;
  }

  return (
    <div className={className}>
      {data.map((bio) => {
        return (
          <BiographyBlock title={bio.title} children={bio.body} key={bio.id} className="my-5" />
        );
      })}
    </div>
  );
};

const BiographyBlock = ({
  title,
  children,
  className,
}: {
  title: string;
  children: string | ReactNode;
  className: string;
}) => {
  return (
    <div
      className={`relative h-full overflow-hidden rounded-lg bg-gray-100 bg-opacity-75 px-8 py-12 dark:bg-gray-800 dark:bg-opacity-40 ${className}`}
    >
      <h1 className="title-font mb-3 text-xl font-medium text-gray-900 dark:text-white sm:text-2xl">
        {title}
      </h1>
      <p className="leading-relaxed" style={{ whiteSpace: 'pre-line' }}>
        {children}
      </p>
    </div>
  );
};

export default Biography;
