import { Helmet } from 'react-helmet-async';
import { HomePageTheme } from '@youfoundation/transit-lib';
import useSiteData from '../../../hooks/siteData/useSiteData';
import HomeClassic from './Classic/HomeClassic';
import HomeContent from './Content/HomeContent';
import HomeCover from './Cover/HomeCover';

const Home = () => {
  const { home, owner } = useSiteData().data ?? {};

  if (!home) {
    return <></>;
  }

  const renderBody = () => {
    if (home?.template === HomePageTheme.SocialClassic.toString()) {
      return <HomeClassic leadText={home.leadText} />;
    } else if (home?.template === HomePageTheme.ContentProducer.toString()) {
      return <HomeContent leadText={home.leadText} />;
    } else {
      return <HomeCover leadText={home?.leadText} />;
    }
  };

  return (
    <>
      <Helmet>
        <title>{owner?.firstName ?? 'Home'} | Youniverse</title>
      </Helmet>
      {renderBody()}
    </>
  );
};

export default Home;
