import { useQuery } from '@tanstack/react-query';
import { ApiType, BlogContent, PublicClient } from '@youfoundation/transit-lib';
import { convertTextToSlug } from '../../helpers/common';
import useAuth from '../auth/useAuth';

type useBlogPostsProps = {
  channelId?: string;
};

export interface BlogContentVm extends BlogContent {
  itemKey?: string;
}

const useBlogPosts = ({ channelId }: useBlogPostsProps) => {
  const { getSharedSecret } = useAuth();
  const pageSize = 10;

  const fetchBlogData = async ({ channelId }: { channelId?: string }) => {
    const client = new PublicClient({ api: ApiType.YouAuth, sharedSecret: getSharedSecret() });

    // Blog posts
    let foundBlogPosts: BlogContentVm[];

    // If located in a specific channel, only fetch those, otherwise fallback to recents;
    if (channelId) {
      const fileData = await client.fileReadOnlyProvider.GetFile('blogs.json');
      if (fileData.has(channelId)) {
        foundBlogPosts =
          fileData.get(channelId)?.map((entry) => {
            const item = entry.payload;

            return {
              ...item,
              slug: convertTextToSlug(typeof item.caption === 'string' ? item.caption : ''),
              itemKey: `${item.channelId}_${item.id}`,
            } as BlogContentVm;
          }) ?? [];
      } else {
        if (fileData.entries.length > 1) {
          foundBlogPosts = [];

          for (const channelId of fileData.keys()) {
            foundBlogPosts = [
              ...foundBlogPosts,
              ...(fileData.get(channelId)?.map((entry) => {
                const item = entry.payload;

                return {
                  ...item,
                  slug: convertTextToSlug(typeof item.caption === 'string' ? item.caption : ''),
                  itemKey: `${item.channelId}_${item.id}`,
                } as BlogContentVm;
              }) ?? []),
            ];

            // Sorted descending
            foundBlogPosts.sort((a, b) => b.dateUnixTime - a.dateUnixTime);
          }
        } else {
          foundBlogPosts = (
            await client.blogPostReadonlyProvider.getPosts(
              channelId,
              'Article',
              undefined,
              pageSize
            )
          ).posts;
        }
      }
    } else {
      foundBlogPosts = await client.blogPostReadonlyProvider.getRecentPosts('Article', pageSize);
    }

    // Process blog posts (Only until fixed in data upload)
    const blogPosts = clearDuplicateBlogs(foundBlogPosts).map((item) => {
      return {
        ...item,
        slug: convertTextToSlug(item.caption),
        itemKey: `${item.channelId}_${item.id}`,
      };
    });

    return { blogPosts };
  };

  return useQuery(['blogs_recents', channelId], () => fetchBlogData({ channelId }), {
    refetchOnMount: false,
    refetchOnWindowFocus: false,
    onError: (er) => {
      console.log(er);
    },
  });
};

//// TODO Remove these temporary helpers:
const clearDuplicateBlogs = (blogs: BlogContent[]) => {
  return blogs.reduce(
    (returnVal, currBlog) =>
      returnVal.some((blog) => blog.id === currBlog.id && blog.channelId === currBlog.channelId)
        ? returnVal
        : [...returnVal, currBlog],
    <BlogContent[]>[]
  );
};

export default useBlogPosts;
