import ProfileHero from '../Common/ProfileHero/ProfileHero';
import ChannelTeaser from '../Common/ChannelTeaser/ChannelTeaser';
import useChannels from '../../../../hooks/blog/useChannels';
import Biography from '../Common/Biography/Biography';
import { t } from '../../../../helpers/i18n/dictionary';
import Arrow from '../../../Icons/Arrow/Arrow';
import ConnectionTeaser from '../Common/ConnectionTeaser/ConnectionTeaserItem/ConnectionTeaserItem';
import ConnectionTeaserAll from '../Common/ConnectionTeaser/ConnectionTeaserAll/ConnectionTeaserAll';

const HomeClassic = (props: { leadText?: string }) => {
  const { channels } = useChannels().data ?? {};

  return (
    <>
      <ProfileHero />
      <section className="body-font text-gray-600 dark:text-gray-400">
        {props?.leadText && (
          <div className="container mx-auto px-5 pt-12">
            <div className="lg:w-2/3">{props.leadText}</div>
          </div>
        )}
        <div className="container mx-auto px-5">
          <Biography className="my-20 lg:w-2/3" />

          <div className="my-20">
            <h2 className="mb-5 text-3xl">{t('blog')}</h2>
            <div className="-mx-4 flex flex-wrap">
              {channels?.map((channel) => {
                return (
                  <ChannelTeaser
                    key={channel.channelId}
                    channel={channel}
                    className={'w-full p-4 lg:w-1/2'}
                  />
                );
              })}
            </div>
          </div>
          <div className="my-20">
            <h2 className="mb-5 text-3xl">{t('Connections')}</h2>
            <div className="-m-3 flex flex-wrap">
              {Array.from('x'.repeat(5)).map((item, index) => {
                return <ConnectionTeaser key={index} />;
              })}
              <ConnectionTeaserAll />
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default HomeClassic;
