import { useQuery } from '@tanstack/react-query';
import {
  ApiType,
  BlogConfig,
  PublicClient,
  HomePageConfig,
  TargetDrive,
  DataUtil,
} from '@youfoundation/transit-lib';
import useAuth from '../auth/useAuth';

const defaultDrive: TargetDrive = {
  alias: HomePageConfig.BlogMainContentDriveId.toString(),
  type: BlogConfig.DriveType.toString(),
};

const useTinyThumb = (imageFileId: string | undefined, imageDrive?: TargetDrive) => {
  const { getSharedSecret } = useAuth();

  const fetchImageData = async (imageFileId: string | undefined, imageDrive?: TargetDrive) => {
    if (imageFileId === undefined || imageFileId === '') {
      return;
    }

    const client = new PublicClient({ api: ApiType.YouAuth, sharedSecret: getSharedSecret() });

    // Look for tiny thumb in already fetched data:
    const thumbFromStaticFile = await client.fileReadOnlyProvider.getFileEntryFromCache(
      imageFileId
    );
    if (thumbFromStaticFile?.[0]?.header.fileMetadata.appData.previewThumbnail) {
      const previewThumbnail = thumbFromStaticFile[0].header.fileMetadata.appData.previewThumbnail;
      const buffer = DataUtil.base64ToUint8Array(previewThumbnail.content);
      const url = window.URL.createObjectURL(new Blob([buffer]));

      return {
        naturalSize: { width: previewThumbnail.pixelWidth, height: previewThumbnail.pixelHeight },
        sizes: thumbFromStaticFile[0].header.fileMetadata.appData.additionalThumbnails ?? [],
        url,
      };
    }

    return await client.mediaProvider.getDecryptedThumbnailMeta(
      imageDrive ?? defaultDrive,
      imageFileId
    );
  };

  return useQuery(
    ['tinyThumb', imageFileId, imageDrive],
    () => fetchImageData(imageFileId, imageDrive),
    {
      refetchOnMount: false,
      refetchOnWindowFocus: false,
      staleTime: Infinity,
    }
  );
};

export default useTinyThumb;
