import { TargetDrive, ThumbSize } from '@youfoundation/transit-lib';
import { useEffect, useRef, useState } from 'react';
import useImage from '../../hooks/image/useImage';
import useTinyThumb from '../../hooks/image/useTinyThumb';
import { useIntersection } from '../../hooks/intersection/useIntersection';

interface ImageProps {
  targetDrive: TargetDrive;
  fileId: string | undefined;
  cover?: boolean;
  className?: string;
  alt?: string;
  title?: string;
}

const Image = ({ targetDrive, fileId, cover, className, alt, title }: ImageProps) => {
  const [isInView, setIsInView] = useState(false);
  const [loadSize, setLoadSize] = useState<ThumbSize | 'full' | undefined>(undefined);
  const imgRef = useRef<HTMLImageElement>(null);
  const { data: thumbMeta } = useTinyThumb(isInView ? fileId : undefined, targetDrive);
  const {
    fetch: { data: imageUrl },
    testCache,
  } = useImage(
    loadSize !== undefined ? fileId : undefined,
    targetDrive,
    loadSize !== 'full' ? loadSize : undefined
  );

  useIntersection(imgRef, () => {
    if (fileId && testCache(fileId, targetDrive)) {
      findLoadOptimalSize();
    }
    setIsInView(true);
  });

  useEffect(() => {
    if (thumbMeta && thumbMeta.url?.length) {
      // Set to load full, with timeout to allow other tinies to load
      setTimeout(() => {
        findLoadOptimalSize();
      }, 100);
    }
  }, [thumbMeta]);

  const findLoadOptimalSize = () => {
    if (!thumbMeta?.sizes?.length || !imgRef.current) {
      setLoadSize('full');
      return;
    }

    const targetWidth = imgRef.current?.clientWidth;
    const targetHeight = imgRef.current?.clientHeight;

    const matchingSize = thumbMeta.sizes.find((size) => {
      return targetWidth < size.pixelWidth && targetHeight < size.pixelHeight;
    });

    setLoadSize(matchingSize);
  };

  const state: 'idle' | 'loading-tiny' | 'tiny' | 'loading-full' | 'loaded' = loadSize
    ? imageUrl
      ? 'loaded'
      : 'loading-full'
    : isInView
    ? thumbMeta && thumbMeta.url?.length
      ? 'tiny'
      : 'loading-tiny'
    : 'idle';

  return (
    <figure
      className={`${
        className?.indexOf('absolute') !== -1 ? '' : 'relative'
      } overflow-hidden ${className}`}
    >
      {state !== 'loading-tiny' ? (
        <img
          src={state === 'loaded' ? imageUrl : thumbMeta?.url}
          alt={isInView && thumbMeta?.url ? alt : ' '}
          className={`${state !== 'loaded' && 'blur-[50px]'} ${
            cover ? 'h-full w-full object-cover' : ''
          }`}
          title={
            title ||
            `${fileId}@${
              loadSize !== undefined && loadSize !== 'full'
                ? `${loadSize.pixelWidth}x${loadSize.pixelHeight}`
                : loadSize
            }`
          }
          ref={imgRef}
          width={thumbMeta?.naturalSize?.width}
          height={thumbMeta?.naturalSize?.height}
        />
      ) : null}
    </figure>
  );
};

export default Image;
