import { BuiltInProfiles, HomePageConfig, ProfileConfig } from '@youfoundation/transit-lib';
import useSiteData from '../../../../../hooks/siteData/useSiteData';
import Image from '../../../../Image/Image';
import Links from '../../../../Layout/Links/Links';
import Socials from '../../../../Layout/Socials/Socials';

const ProfileHero = () => {
  const { owner, home, social } = useSiteData().data ?? {};

  const targetDrive = {
    alias: BuiltInProfiles.StandardProfileId.toString(),
    type: ProfileConfig.ProfileDriveType.toString(),
  };

  const showProfileImage = owner?.profileImageId && targetDrive;

  return (
    <section className="bg-slate-100 dark:bg-slate-800">
      <div className="relative h-[27vh] min-h-[330px]">
        <Image
          fileId={home?.headerImageFileId}
          targetDrive={HomePageConfig.HomepageTargetDrive}
          className="absolute inset-0"
          cover={true}
        />
        <div className="container absolute top-0 left-0 right-0 bottom-0 mx-auto flex justify-center px-5 md:block">
          {showProfileImage ? (
            <div className="absolute bottom-[-4.5rem] h-60 w-60 overflow-hidden rounded-full border-8 border-neutral-200 md:bottom-[-7.5rem] ">
              <Image
                fileId={owner?.profileImageId}
                targetDrive={targetDrive}
                className="h-full w-full"
                cover={true}
              />
            </div>
          ) : null}
        </div>
      </div>
      {/* min height of 7.5 rem to ensure sufficient spacing after the hero picture to support the offset of the profile picture*/}
      <div className="container mx-auto flex min-h-[8.5rem] px-5 dark:text-white xl:h-[8.5rem]">
        <div className={`my-auto w-full ${showProfileImage ? 'pt-[6rem] md:py-4 md:pl-60' : ''}`}>
          <div className={`flex flex-col ${showProfileImage ? 'md:pl-10' : ''} lg:flex-row`}>
            <h1 className="text-center text-2xl md:text-left">
              {`${owner?.firstName ?? ''} ${owner?.surName ?? ''}`}
              <br />
              <small>{home?.tagLine}</small>
            </h1>
            <div className="mt-10 mb-5 flex flex-col justify-center md:my-auto md:ml-auto">
              <Socials
                socialHandles={social}
                className="mt-4 justify-center sm:mt-0 md:ml-auto md:justify-start"
              />
              <Links className="mt-3 justify-center md:justify-end" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ProfileHero;
