import ChannelTeaser from '../Common/ChannelTeaser/ChannelTeaser';
import useSiteData from '../../../../hooks/siteData/useSiteData';
import { BuiltInProfiles, ProfileConfig } from '@youfoundation/transit-lib';
import Image from '../../../Image/Image';
import useChannels from '../../../../hooks/blog/useChannels';
import Links from '../../../Layout/Links/Links';
import ConnectionTeaser from '../Common/ConnectionTeaser/ConnectionTeaserItem/ConnectionTeaserItem';
import ConnectionTeaserAll from '../Common/ConnectionTeaser/ConnectionTeaserAll/ConnectionTeaserAll';

const HomeCover = (props: { leadText?: string }) => {
  const { owner, home } = useSiteData().data ?? {};
  const { channels } = useChannels().data ?? {};

  if (!owner) {
    return <></>;
  }

  const targetDrive = {
    alias: BuiltInProfiles.StandardProfileId.toString(),
    type: ProfileConfig.ProfileDriveType.toString(),
  };

  return (
    <>
      <section className="body-font my-auto">
        <div className="container mx-auto">
          <div className="lg:flex">
            <div className="mb-12 lg:w-1/2">
              {owner?.profileImageId && targetDrive ? (
                <Image
                  fileId={owner?.profileImageId}
                  targetDrive={targetDrive}
                  className="w-full max-w-[none] sm:mx-auto md:max-w-[30rem] lg:max-w-[none]"
                />
              ) : null}
            </div>

            <div className="flex h-full flex-col px-5 lg:w-1/2">
              <div className="px-5">
                <h1 className="mb-4 text-2xl dark:text-white">
                  {owner?.firstName ?? ''} {owner?.surName ?? ''}
                  <br />
                  <small>{home?.tagLine ?? ''}</small>
                </h1>
                {props?.leadText ?? ''}
              </div>
              <div className="mt-auto -mb-4 py-12">
                {channels?.map((channel) => {
                  return (
                    <ChannelTeaser
                      key={channel.channelId}
                      channel={channel}
                      className={'w-full py-4'}
                    />
                  );
                })}
                <Links className="justify-center" style="secondary" />
              </div>
            </div>
          </div>
          <div className="mt-16 mb-20 px-5">
            <div className="-m-3 flex flex-wrap">
              {Array.from('x'.repeat(5)).map((item, index) => {
                return <ConnectionTeaser key={index} />;
              })}
              <ConnectionTeaserAll />
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default HomeCover;
