import { t } from '../../../../helpers/i18n/dictionary';
import useChannels from '../../../../hooks/blog/useChannels';

import { BlogChannelTeaser } from '../Common/BlogChannelTeaser/BlogChannelTeaser';
import ConnectionTeaserAll from '../Common/ConnectionTeaser/ConnectionTeaserAll/ConnectionTeaserAll';
import ConnectionTeaser from '../Common/ConnectionTeaser/ConnectionTeaserItem/ConnectionTeaserItem';
import ProfileHero from '../Common/ProfileHero/ProfileHero';

const HomeContent = (props: { leadText?: string }) => {
  const { channels } = useChannels().data ?? {};

  return (
    <>
      <ProfileHero />
      <div className="container mx-auto px-5 pt-12">{props.leadText}</div>
      <div className="py-12">
        {channels?.map((channel) => {
          return (
            <BlogChannelTeaser key={channel.channelId} title={channel.name} channel={channel} />
          );
        })}
      </div>
      <div className="container mx-auto px-5">
        <div className="mt-0 mb-10">
          <h2 className="mb-5 text-2xl">{t('Connections')}</h2>
          <div className="-m-3 flex flex-wrap">
            {Array.from('x'.repeat(5)).map((item, index) => {
              return <ConnectionTeaser key={index} />;
            })}
            <ConnectionTeaserAll />
          </div>
        </div>
      </div>
    </>
  );
};

export default HomeContent;
